<template>
  <div class="col-lg-8 col-md-10 mx-auto text-center myClass">
    <div class="bilgiler">
      <h2 class="section-heading mx-auto">{{ data.title }}</h2>
      <br /><br />

      <h4>
        {{ data.description }}
      </h4>
      <br /><br />
    </div>

    <div v-show="data.commits.length > 0" class="container">
      <div
        v-for="commit in data.commits"
        :key="commit.index"
        class="card mb-3"
        v-show="commit.description != null"
      >
        <i
          @click.prevent="clickDelete(commit.id)"
          :class="
            user.isAuthenticated == true && commit.userId == user.id || user.isAdmin==true
              ? ''
              : 'delete'
          "
          class="fas fa-times-circle"
        ></i>
        <div class="card-body">
          <h5 class="card-title">{{ commit.description }}</h5>
          <p class="card-text"></p>
        </div>
        <div class="card-footer text-muted">
          <p>{{ new Date(commit.updatedAt) }}</p>
          <h4 class="text-muted">{{ commit.user.name }}</h4>
        </div>
      </div>
    </div>

    <blockquote class="blockquote">
      Ltfen Deerli Bilgilerinizi Giriniz Bilgi Paylatka oalr !
    </blockquote>
    <div class="row mb-1">
      <textarea name="commit" cols="90" rows="5" v-model="commit"></textarea>
    </div>
    <div class="row justify-content-end">
      <button @click.prevent="addCommitt" class="btn btn-info">
        Yorum Ekle
      </button>
    </div>
  </div>
</template>

<script>
//import PostService from "../services/PostAPI";
import { mapActions, mapGetters } from "vuex";
export default {
  data(){
    return{
      commit:""
    }
  },
  methods: {
    ...mapActions(["getByName","addCommit","deleteCommit"]),
    ...mapGetters(["getOnePost","getUser"]),
    addCommitt(){
      if(this.commit!=""){
        //postId Gidecek ve commit gidecek
        let commitInstance={
          postId:this.data.id,
          commit:this.commit,
        }
        this.addCommit(commitInstance);
      }else{
        this.$alert("Ltfen Bieyler Giriniz !","Uyar","info")
      }
    },
    clickDelete(commitId){
      this.$confirm("Yorumunu Silmek stiyor musun ?","Uyar","warning").then(data=>{
        if(data){
          let commit_data={
            commitId:commitId,
            postId:this.data.id
          }

          this.deleteCommit(commit_data);
        }else{
           console.log("sdfsd")
        }
      })
    }
  },
  computed: {
    data() {
      return this.getOnePost();
    },
    user(){
      return this.getUser();
    }
  },
  created() {
    this.getByName(this.$route.params.id);
    window.scrollTo(0, 0);
  },
};
</script>


